import '../main/style.css'
import guacamole from '../../imagens/guacamole.jpg'
import burrito from '../../imagens/burrito.jpg'
import tequila from '../../imagens/tequila.jpg'
import nacho from '../../imagens/nacho.jpg'
import tortilhas from '../../imagens/tortilhas.jpg'
import churros from '../../imagens/churros.jpg'
import maria from '../../imagens/maria.png'
import morte from '../../imagens/morte.png'
import Carnaval from '../../imagens/Carnaval.jpg'
import grito from '../../imagens/grito.jpg'
import  guadalupe from '../../imagens/guadalupe.jpg'
import DMortos from '../../imagens/DMorte.jpg'
import anoNovo from '../../imagens/anoNovo.jpg'
import calaveras from '../../imagens/calaveras.jpg'
import roupaUm from '../../imagens/roupaUm.png'
import roupaDois from '../../imagens/roupaDois.jpg'
import roupaTres from '../../imagens/roupaTres.png'
import roupaQuatro from '../../imagens/roupaQuatro.jpg'
import roupaCinco from '../../imagens/roupaCinco.png'
import  roupaSeis from '../../imagens/roupaSeis.png'
import roupaSete from '../../imagens/roupaSete.png'
import roupaOito from '../../imagens/roupaOito.png'
import roupaNove from '../../imagens/roupaNove.png'
function Culture(){
    return(
        <div>
            <div className="culture">
            <div className="Cabeca">Culture</div>

        <div className="topico">

            <div className="titulo">
                Typical foods
                </div>
            <div className="texto"> One of the great pleasures of being human is to try and eat good food, isn't it? But what about when the subject is typical Mexican food? The first thing that comes to our minds is something very spicy and with a lot of seasoning, but it doesn't stop there! So here are the main typical foods of this country so well known to us.<br/>
            <br/><img src={`${guacamole}`} alt="guacamole"/><br/>
            Easy to prepare, this is one of the most well-known foods for us Brazilians and also most famous for Mexicans. It uses avocado, tomato, onion, and some spices, which form a puree that is usually accompanied by nachos. <br/>
            <br/><img src={`${tortilhas}`} alt="tortillas"/><br/>This food is also very present in Mexican cuisine, which is basically bread baked in a disc shape and what makes it different is the preparation that uses spices and delicacies very present in Mexico.<br/>
            <br/><img src={`${burrito}`} alt="burrito"/><br/>With the same preparation as tortillas, it becomes a burrito when made in rolled format and with plenty of filling, such as: meat, cheese, and seasonings. Surely, everywhere you go, you can find this delicious dish. <br/>
            <br/><img src={`${nacho}`} alt="nacho"/><br/>Being another version of the tortillas, the nacho is also a bread, but in a triangular shape and fried, popularly we also have the brand Doritos, which sell this recipe everywhere. It is usually accompanied by cheese, tomatoes, or even guacamole.<br/>
            <br/><img src={`${churros}`} alt="churros"/><br/>This candy is very well known worldwide, but it is present in the typical Mexican cuisine. They are filled and fried with dulce de leche and chocolate and have a crunchy shell on the outside and soft inside, very irresistible. <br/>
            <br/><img src={`${tequila}`} alt="tequila"/><br/>To end a list of typical foods, we cannot fail to mention the very famous tequila. Equivalent to a cachaça, tequila can be served with lemon, salt, and drinks, and is well known to us all for its strong presence. <br/>
             </div>

            <div className="titulo">
                Religion
                </div>
            <div className="texto"> Mexico does not have an official religion. But, Catholicism is the dominant faith and deeply culturally pervasive, is estimated over 80% of the population identifies as Catholic. The Catholic Mexican see Catholicism as part of their identity, passed on through the family and nation like cultural heritage. Not all Mexicans attend church services regularly. Religiosity is most visible in festivities, events and also the placement of idols throughout people’s homes and public places. They are generally quite tolerant of other faiths. The numbers of Pentecostal Christians, Evangelical Christians and Jehovah’s Witnesses is growing more and more. The patron saint of Mexico as the Virgin of Guadalupe, she is like another version of the Virgin Mary, very popular in Brazil. She is the most important Mexican icon and religious cultural symbol, is believed to be the “mother” of all Mexicans.<br/> 
            <img src={`${maria}`} alt="maria"/><br/>
            There are also those who believe in myths and magic, some Mexicans have a fear of black magic, curses, witches and evil eyes. Healers attend most of these people to protect them from a spell or dissolve it. People also seek fortune-tellers that read candles and tarot cards to interpret events in their daily and professional lives. These traditions are still popular and practiced in several places in Mexico.<br/>
            With growing popularity among the poorest, the Saint Death is a female figure who promotes healing, protection and favour to her followers. Her followers believe she is less judgmental and feel more comfortable asking her favours that may be morally ambiguous in the eyes of the Catholic Church. <br/>
            <img src={`${morte}`} alt="morte"/><br/>
             </div>
            <div className="titulo">
                Festivals
                </div>
            <div className="texto"> Parties are, in general, a way to have fun and spend happy times. In addition, there are also cultural and historical contexts that make a party not just something simple, but memorable and exciting, and Mexico is no different. The country is marked by celebrations, festivities, colors, and customs that are very striking in the country. Learn now about the main festivals of this festive country known by us all.<br/>
            <br/> <b>Dia de los Muertos</b><br/>
            <img src={`${DMortos}`} alt="Dia dos mortos"/><br/>
            <br/>To start this list, we have the traditional Dia de los Muertos party, very well known around the world, which starts at the end of October and only ends on the second day of November. They believe and celebrate that on these days the dead come back to life and have a divine permission to visit friends and family, so those who are alive decorate their homes with flowers, candles, pictures and culinary dishes. As much has been seen, people also usually dress up as skulls and celebrate in the streets. <br/>
            <br/><b>Carnaval</b><br/>
            <img src={`${Carnaval}`} alt="Carnaval"/><br/>
            <br/>Surprised or not, carnival is not a party exclusively typical of Brazil. In Mexico, carnival is celebrated in February and although it is a day of parties, dancing and costumes, it is not considered a holiday among them. This party attracts many tourists from around the world, for being something very striking full of colors and animation.<br/>
            <br/><b>Grito da Independência - El Grito</b><br/>
            <img src={`${grito}`} alt="grito"/><br/>
            One of the most important celebrations in Mexico, El Grito is held every September 15. In the event, Mexicans gather in public places such as plazas and a local authority yells "Viva!" and everyone celebrates the whole day with fireworks, music, and typical foods. They usually wear the colors of the country's flag on this day.  <br/>
            <br/><b>Dia de Nossa Senhora de Guadalupe</b><br/>
            <img src={`${guadalupe}`} alt="guadalupe"/><br/>
            <br/>A religious festival instituted in 1531 and currently celebrated every December 12, the day of the country's patron saint is celebrated with much respect and emotion. What makes this celebration exciting is the strong presence of the country's religious culture and the multitude around its belief and faith. On the day, food and drinks are served, as well as religious rites.<br/>
            <br/><b>Año nuevo</b><br/>
            <img src={`${anoNovo}`} alt="Ano Novo"/><br/>
            <br/>New Year's Eve is also celebrated with great importance and festivity in Mexico. Cities like Acapulco and Cancun are well known for receiving people from many places to watch the fireworks. Being a traditionally partying country, this becomes very striking.<br/>
            <br/><b>Festival de Las Calaveras</b><br/>
            <img src={`${calaveras}`} alt="calaveras"/><br/>
            <br/>Followed by Dia de Los Muertos, the Calaveras Festival is a festival that welcomes more than 800,000 people each year to celebrate. Usually, people dress up as skeletons and colorful skulls and parade through the streets of Aguascalientes, ending at Cerro del Muerto.<br/>

            </div>

            <div className="titulo">
                Clothes
                </div>
            <div className="texto">
            The Mexican clothing is very bright and beautiful, there is a wide variety designs of traditional costume. These are used for celebrations, holidays, ceremonies and festivals.
            The main characteristics of Mexican national attire are:<br/>
            <ul>
            -	Sun protection<br/>
            -	Brightness<br/>
            -	Moderate modesty<br/>
            </ul>
            <br/>Because of the sun, sombrero has such wide brims and the clothing itself is made of natural materials. The fabric for garments is mostly colorful and heavily embroidered; a lot of colorful ribbons are used. The clothing is elegant, not shameless or vulgar at all, skirts are long, blouses are delicate, and flowers are often used to beautify the costume, hairdo and headdress.  <br/>
            <br/>The women’s clothing has changed during centuries, but traditional Mexican men’s costume has changed very little. Most of costumes were formed by mixing the culture os Native Americans and European people, mostly Spanish new settlers. <br/>
            <img src={`${roupaUm}`} alt="roupaUm"/><br/>
            <br/>The clothing in Mexico is made of various fabrics, like:<br/>
            <ul>
            -	Wool, cotton, silk, agave, and bark. <br/>
            </ul>
            <br/>Usually the colors of traditional dress are:<br/>
            <ul>-	Red, brown, green and yellow.<br/></ul>
            <br/>They used to dye their clothes with natural components, but today they don’t have to do that. <br/>
            The most popular men’s pieces of clothing in Mexico are:<br/>
            <ul>
                <li><b>Sarape:</b> a vibrantly colored garment with a fringe. Is used as a shawl-like coat and also as a blanket.</li>
                <li><b>Charro suit:</b> a special beautifully embroidered suit which is worn on the Day of the Dead.</li>
                <li><b>Sombrero:</b> a Mexican hat with a wide brim. This hat is made of straw or heavy felt, and protects the body from the sun. The most recognizable part of clothing around the world.</li>
                <li><b>Guayabero:</b> a lightweight shirt or suit which is used both as a casual and formal dress. Is made of cotton and very often is decorated with heavy embroidery, and is perfect attire for Mexican heat.</li>            
                <li><b>Baja jacket:</b> a Mexican hooded shirt which is very popular among Mexican and American youth. Mostly men wear it, but women sometimes also do. Is loved by hippies. Usually rather warm, it has a hood and a large pocket situated on the belyy, is often colorful, with striped patterns and looks very youthful.</li>
                <li><b>Poncho:</b> it is a warm outerwear, there are many different variations of poncho. For example, classical poncho is a simple piece of cloth with a hole for the head; it doesn’t have sleeves, but sometimes has a hood. Rainproof poncho is fitted with fasteners for the sides or has openings for the arms. It is more typical for men to wear poncho, but some women use it too.</li>
           </ul>
           <img src={`${roupaDois}`} alt="roupaDois"/><br/>
           <br/>There are several types of shoes, some of them are:<br/>
           <ul>
               <li><b>Huaraches:</b> are the most common shoes in Mexico, they are sandals, rather simple to make. There is a great variety of these shoes, usually they are made of leather or synthetic material and always handmade. </li>
               <li><b>Mexican pointy boots:</b> They have narrow and elongated toes. Sometimes, their toes are up to 1.5 meter long and curved up to the knees of a man. Are very colorful, often decorated with such elements as flashing LED lights, disco balls and even mirrors, and often worn by bands, singers and dancers</li>
           </ul>
           <img src={`${roupaTres}`} alt="roupaTres"/><br/>
           <b>TRADITIONAL WOMEN’S PIECES OF CLOTHING</b><br/>
           The most popular women’s pieces of clothing in Mexico are:<br/>
           <ul>
               <li><b>Huipil:</b> a sleeveless tunic, made from cotton or wool. It is worn with a skirt. The design depends on a region, beliefs, marital status and so on.</li>
               <li><b>Quechquémitl:</b> is a festive kind of poncho. It is worn for parties, holidays and festivals. Should be handmade from handwoven cloth. Also it is often heavily embroidered, and is very colorful and beautiful. </li>
               <li><b>Rebozo:</b> is a shawl/scarf made from cotton, wool, or silk. The color and pattern of rebozo represent the region and community. Is used not only as clothes, but also to carry products and goods, even babies.</li>
               <li><b>Mexican skirts:</b> can be ankle-long or knee-long. They are made from cotton, wool, silk and lace. Very often they are wide, bright and embroidered. </li>
               <li><b>Blouses:</b> There is a great variety of blouses, but usually they are beautifully decorated with lace, beads, colorful patterns, and embroidery. </li>
           </ul>
           <img src={`${roupaQuatro}`} alt="roupaQuatro"/><br/>
            <br/><b>TRADITIONAL MEXICAN DRESS DESIGNS</b><br/>
            The Mexican women wear very beautiful and feminine dresses and every corner of Mexico has its own traditional dress design:<br/>
            <ul>
                <li><b>Jalisco Traditional Dress:</b>It is also called Escaramuza dress. <br/> <img src={`${roupaCinco}`} alt="um"/></li>
                <li><b>Michoacan Traditional Dress:</b>Very sophisticated.<br/> <img src={`${roupaSeis}`} alt="dois"/></li>
                <li><b>Tabasco Traditional Dress:</b>The women in this region like floral embroidery, bright ribbons and hair decorations with flowers<br/> <img src={`${roupaSete}`} alt="tres"/></li>
                <li><b>Campeche Traditional Dress:</b>Formed under the Spanish influence.<br/> <img src={`${roupaOito}`} alt="quatro"/></li>
                <li><b>Chiapas Traditional Dress:</b>Are handmade, and women from the town Chiapa de Corzo make these dresses.<br/> <img src={`${roupaNove}`} alt="cinco"/></li>
            </ul>
            </div>
        </div>
        </div>
        </div>
    )
}
export default Culture