import '../main/style.css'

function Curiosities(){
    return(
        <div>
            <div className="curiosities">
            <div className="Cabeca">Curiosities</div>

        <div className="topico">

            <div className="titulo">
             There are women-only cars in the subway.
                </div>
            <div className="texto"> In Mexico City, entry of men is illegal in some carriages, creating a safer space for women. The crime rate of the place is standard, and with this action they try to help combat some problematic situations that could occur.

</div>
            <div className="titulo">
                Quirky foods.
                </div>
            <div className="texto">  In any market in Mexico you can find spiders, locusts and even scorpions for sale to take home and eat.

            </div>

            <div className="titulo">
            Mexico has the oldest university.
                </div>
            <div className="texto">  Many feel that Harvard is the oldest university in North America, but in reality the oldest university is the National Autonomous University of Mexico in Mexico City. It opened in 1551 as the Royal and Pontifical University of New Spain, but unfortunately it was closed in 1867 during the dictatorship. Then it was reopened during the revolution, so unlike Harvard it hasn't been open that long, but it's been around longer!

            </div>
            
        </div>
        </div>
        </div>
    )
}
export default Curiosities