import '../main/style.css'
import elarco from '../../imagens/elArco.jpg'
import Teotihuacan from '../../imagens/teotihuacan.jpg'
import Park from '../../imagens/xcaretPark.jpg'
import Tulum from '../../imagens/Tulum.jpg'
import cenote from '../../imagens/CenoteDosOjos.jpg'

function TH(){
    return(
        <div>
            <div className="culture">
            <div className="Cabeca">Tourist Hotspots</div>

        <div className="topico">

            <div className="titulo">
            Cenote Dos Ojos
                </div>
            <div className="texto"> 
            <img src={`${cenote}`} alt="Cenote dos Ojos"/><br/>
            <br/>Cenote Dos Ojos is one of the largest underwater cave systems in the country. The name refers to the two holes connected by a long passage. The water in caves is filtered by limestone, so it is very clear. The place is popular with snorkelers and more serious divers, some of whom dive into the depths of the underwater cave.
             </div>

            <div className="titulo">
            Tulum
                </div>
            <div className="texto"> 
            <img src={`${Tulum}`} alt="tulum"/><br/>
            <br/>Tulum is located on the east coast of the Yucatan Peninsula. Tulum was built around 1200 AD, when the Mayan civilization was already in decline. The tropical beach setting, however, makes this an impressive attraction. The place has accommodations: from the simplest to the most luxurious.
             </div>

            <div className="titulo">
            El Arco
                </div>
            <div className="texto"> 
            <img src={`${elarco}`} alt="El Arco"/> <br/>
            <br/>Is an incredible arch found in Cabo San Lucas. A walk along the beach gives the opportunity to take fantastic photographs of El Arco, not to mention views of the blue waters, the gray whales that can often be seen in the ocean and other rock formations along the coast.
            </div>

            <div className="titulo">
            Xcaret Archaeological Park
                </div>
            <div className="texto">
            <img src={`${Park}`} alt="Xcaret Archaeological Park"/><br/>
            <br/>In the city of Cancún is located one of the most visited tourist spots in the country: the Xcaret Archaeological Park. The park has several water activities, walks through green areas, and visitors can observe marine animals!
                  </div>

            <div className="titulo">
            Teotihuacan
                </div>
            <div className="texto"> 
            <img src={`${Teotihuacan}`} alt="Teotihuacan"/> <br/>
            <br/>It is an archaeological site where there are huge pyramids: the Pyramid of the Sun and the Pyramid of the Moon. The site is 50km from Mexico City and is easily located.
            </div>

            
        </div>
        </div>
        </div>
    )
}
export default TH