import './style.css'
import mexico from '../../imagens/mexico.png'
import {MdOutlineHistoryEdu,GrLanguage,FaPlaneDeparture,GiMagnifyingGlass,FaTheaterMasks} from 'react-icons/all'
function Main() {
  return (
    <div className="main">

      <div className="mexico">
        <img className="bandeira" src={mexico} alt="bandeira"/>
        <p className="mexicoPalavra">Mexico</p>
      </div>
      <div className="boxes">
      <a href='/history'>
      <div className="box">
        <MdOutlineHistoryEdu className="icon"/>
          History
        </div></a>
        <a href='/about'>
        <div className="box">
          <GrLanguage className="icon"/>
          About
        </div></a>
        <a href='/culture'>
        <div className="box">
          <FaTheaterMasks className="icon"/>
          Culture
        </div></a>
        <a href="/curiosities">
        <div className="box">
          <GiMagnifyingGlass className="icon"/>
          Curiosities
        </div></a>
        <a href="/th">
        <div className="box">
          <FaPlaneDeparture className="icon"/>
         Tourist hotspots
        </div></a>
      </div>
      <footer className="pe">Made by: Damaris Santana, Gabriel Alves, Gabriely Cristian, Isadora Santos, Kemilly Teixeira</footer>

    </div>
  );
}

export default Main;
